import { defineComponent } from '@vue/composition-api'
import StatisicChart from '@/components/statistic-chart/statistic-chart.vue'
import Stub from '@/components/stub/stub.vue'
import { formatNumber, statisticTimeFormat } from '@/components/composition/format-utils'

/**
 * dataToShow по запросу getReport имеет следующую структуру
 * {
 *   // intervals - массив объектов { start: String, end: String, value: string }
 *   // timeUnit - по спеке https://www.chartjs.org/docs/latest/axes/cartesian/time.html
 *   availableTime: {
 *     intervals: [],
 *     // часы:минуты
 *     totalValue: '0:0',
 *     timeUnit: 'week'
 *   },
 *   availableTimePercent: {
 *     intervals: [],
 *     // отличие от времени в разделителе
 *     totalValue: '0.0',
 *     timeUnit: 'week'
 *   },
 *   problemTime: {
 *     intervals: [],
 *     // часы:минуты
 *     totalValue: '0:0',
 *     timeUnit: 'week'
 *   },
 *   notWorkingCameraCount: 0,
 *   totalTime: '0:0'
 * }
 */



export default defineComponent({
  name: 'Reports',
  components: {
    StatisicChart,
    Stub
  },
  setup() {
    const fieldsStatisticCommon = ['totalTime', 'notWorkingCameraCount']
    const filedsChart = ['availableTime', 'availableTimePercent', 'problemTime']
    return {
      fieldsStatisticCommon,
      filedsChart,
      formatNumber,
      statisticTimeFormat
    }
  },
  data() {
    return {
      dataToShow: window.dataToShow,
      reportSettings: window.reportSettings
    }
  },
  computed: {
    totalTimeView() {
      if (this.dataToShow) {
        const result = this.statisticTimeFormat(this.dataToShow.totalTime)
        result.hours = this.formatNumber(result.hours)
        return result
      }
    },
    reportSettingsShow() {
      if (this.reportSettings) {
        const result = { ...this.reportSettings }
        delete result.dateReport
        return result
      }
      return null
    }
  }
})